import React, { Component } from "react"
import Link from "gatsby-link"
import { rhythm } from "../utils/typography"
import PostIcons from "../components/PostIcons"

export default class PostRow extends Component {
    render() {

        const node = this.props.node

        return (
            <div css={{
                marginBottom: rhythm(2),
                display: 'flex',
                alignItems: 'flex-start'
            }} key={node.slug}>
                <div style={{
                    padding: rhythm(.5),
                    order: this.props.side % 2,
                    boxShadow: `
                    inset 1px 1px 0 0 #fff, inset -1px -1px 0 0 #fff,
                    inset 2px 2px 0 0 #333, inset -2px -2px 0 0 #333,
                    inset 3px 3px 0 0 #fff, inset -3px -3px 0 0 #fff`

                }}>
                    <Link to={node.slug}>
                        <img src={node.better_featured_image.source_url} style={{ marginBottom: 0 }} />
                    </Link>
                </div>
                <div style={{
                    padding: rhythm(.5),
                    boxShadow: `inset 0 -1px 0 0 #fff, 
                    inset 0 -2px 0 0 #333, 
                    inset 0 -3px 0 0 #fff`
                }}>

                    <Link to={node.slug} css={{
                        textDecoration: `none`,
                        ":hover": {
                            textDecoration: `underline`,
                        }
                    }}>
                        <h2 dangerouslySetInnerHTML={{ __html: node.title }} style={{ color: `#fff` }} />
                    </Link>
                    <div dangerouslySetInnerHTML={{ __html: node.excerpt }} />
                    <PostIcons node={node} />
                </div>

            </div>
        )
    }
}



