import React, { Component } from "react"
import { Helmet } from "react-helmet"
import PropTypes from "prop-types"
import Link from "gatsby-link"

import './../utils/grid.css'


import PostRow from "../components/PostRow"

import { rhythm } from "../utils/typography"

class ProductsPage extends Component {
  _handleClick() {
    console.log('------------------------------------');
    console.log(`clicked`);
    console.log('------------------------------------');
  }
  render() {
    const data = this.props.data
    const meta = data.site.siteMetadata
    const products = data.allWordpressWpProducts.edges

    return (
      <div>
        <Helmet>
          <title>Products / {meta.title}</title>
          <meta name="description" content={meta.description} />
          <link rel="canonical" href="http://mysite.com/example" />
        </Helmet>


        <h1 style={{color: `#fff`}}>Products</h1>
        <div className="grid">
          {products.map(({ node }, i) => {
            // console.log('------------------------------------');
            // console.log(node);
            let img
            if (node.better_featured_image) {
              img = <img src={node.better_featured_image.source_url} style={{
                maxHeight: `30vmin`,
                margin: `auto`,
                marginBottom: `.5rem`,
                display: `block`
              }} />
            }

            return (
              <div key={i} className="grid-cell" onClick={this._handleClick}>
                {img}
                <Link to={node.slug}>
                  <h3 dangerouslySetInnerHTML={{ __html: node.title }} />
                </Link>
                <div dangerouslySetInnerHTML={{ __html: node.excerpt }} />
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}

export default ProductsPage

// Set here the ID of the home page.
export const pageQuery = graphql`
  query ProductsPageQuery {
    site {
      siteMetadata {
        title
        subtitle
        description
      }
    }
    
    allWordpressWpProducts(sort: { fields: [date] }) {
      edges {
        node {
          title
          excerpt
          slug
          type
          better_featured_image{
            source_url
          }
          categories
        }
      }
    }
  }
`



